<template>
    <div class="card row p-0" style="cursor: pointer;">
        <img :src="`img/UI_Story/${chapter}.png`" class="card-img p-0" :alt="`${chapter}`">
        <div class="card-img-overlay">
            <img :src="`img/UI_Story/UI_Icon_Symbol_${chapter}.png`" :class="`card-img MainStoryIcon ${chapter}`" :alt="`${chapter}`">
        </div>
        <div class="card-img-overlay">
            <h1 :class="`card-title MainStoryTitle ${chapter}`">{{ chapter }}</h1>
            <p :class="`card-text MainStorySubtitle ${chapter}`"></p>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, toRef } from 'vue'
import { useMobile } from "@/store/Mobile"
import { useLanguage } from '@/store/Language'
import { storeToRefs } from 'pinia'
import StoryIndex from '@/assets/json/StoryIndex.json'

export default {
    name: 'StoryCard',
    props: ['chapter'],
    setup(props) {
        const { isMobile } = storeToRefs(useMobile())
        const { language, text } = storeToRefs(useLanguage())
        const lastMainStory = ref(13)
        const chapter = toRef(props, 'chapter')

        const mainStoryIndex = ref(StoryIndex.filter((story) => story.StoryName.includes('Chapter')))

        return {
            isMobile,
            text,
            language,
            lastMainStory,
            mainStoryIndex,
            chapter
        }
    }
}
</script>

