                    <div class="card-img-overlay">
                        <h1 class="card-title text-dark" style="position: absolute; bottom: 2%; right: 4%; text-shadow: 3px 3px 5px white;">Event</h1>
                    </div>
                </div>
            </div>
            <div class="row mx-3" v-if="isMobile">
                <ul class="list-group my-3 p-0 rounded rounded-4">
                    <li class="list-group-item" style="background-color: rgba(255,255,255,0.5)" v-for="index in lastStoryIndex" :key="index" >
                        <a href="#" @click="showStoryContent(storyFiles[index - 1])">
                            <img src="img/UI_Icon_Stage_Book1.png" style="width: 3em" alt="Story">
                            {{ storyBooks[index - 1] }}
                        </a>
                    </li>
                </ul>
            </div>
            <div class="row mx-3" style="position: relative; height: 50vh;" v-else>
                <div class="card book" v-for="index in lastStoryIndex" :key="index" 
                :style="`top: ${node_top[index - 1]}%; left: ${node_left[index - 1]}%;`" @click="showStoryContent(storyFiles[index - 1])">
                    <button type="button" class="btn p-0">
                        <img src="img/UI_Icon_Stage_Book1.png" style="width: 100%" alt="Story">
                    </button>
                    <div class="card-body p-0">
                        <p class="card-text">{{ storyBooks[index - 1] }}</p>
                    </div>
                </div>
                <div class="road" v-for="index in lastStoryIndex - 1" :key="index" 
                :style="`top: ${edge_top[index - 1]}%; left: ${edge_left[index - 1]}%; rotate: ${edge_deg[index - 1]}deg;`">
                    <div class="glass"></div>
                    <div class="p-0 bg-light edge"></div>
                </div>
            </div>
        </div>
        <div class="container d-none p-5 unselectable" id="StoryContent" 
        style="width:100%; height:100%; background-color: rgba(0,0,0,0.33); overflow-y: scroll;"
        @click="displayStory">
            <div style="height: 2rem;"></div>
        </div>
        <div class="container d-none" id="controlBar">
            <button type="button" class="btn rounded-pill controlBtn" @click="hideStoryContent">
                <i class="bi bi-arrow-bar-left text-light"></i>
            </button>
            <button type="button" class="btn rounded-pill controlBtn" @click="autoplay" v-if="!autoFlag">
                <i class="bi bi-fast-forward-circle-fill text-light"></i>
            </button>
            <button type="button" class="btn rounded-pill controlBtn" @click="stopAutoplay" v-else>
                <i class="bi bi-fast-forward-circle-fill text-secondary"></i>
            </button>
            <button type="button" class="btn rounded-pill controlBtn" @click="stopMusic" v-if="musicFlag">
                <i class="bi bi-music-note-beamed text-light"></i>
            </button>
            <button type="button" class="btn rounded-pill controlBtn" @click="playMusic" v-else>
                <i class="bi bi-music-note-beamed text-secondary"></i>
            </button>
            <button type="button" class="btn rounded-pill controlBtn" @click="nextStory" v-if="dialogueIndex + 1 < storyFiles.length">
                <i class="bi bi-skip-forward-fill text-light"></i>
            </button>
            <button type="button" class="btn rounded-pill controlBtn" v-else>
                <i class="bi bi-skip-forward-fill text-secondary"></i>
            </button>
        </div>
        <audio id="storyMusic">
            <source :src="music" type="audio/wav">
        </audio>
