</template>

<script>
/* eslint-disable */
import { ref, watch, onUnmounted } from 'vue'
import { useMobile } from "@/store/Mobile"
import { useLanguage } from '@/store/Language'
import { useSetting } from '@/store/Setting'
import { storeToRefs } from 'pinia'
import axios from 'axios'
import StoryCard from '@/components/StoryCard.vue'
import StoryIndex from '@/assets/json/StoryIndex.json'

export default {
    name: 'CampaignStory',
    components: {
        StoryCard,
    },
    setup() {
        const { isMobile } = storeToRefs(useMobile())
        const { language, text } = storeToRefs(useLanguage())
        const { background, music, counter } = storeToRefs(useSetting())
        const lastMainStory = ref(13)
        const lastStoryIndex = ref(13)
        const edge_top = ref([24, 24, 24, 30, 50, 65, 65, 70, 90, 105, 105, 105 ])
        const edge_left = ref([10, 30, 50, 70, 70, 50, 30, 10, 10, 30, 50, 70])
        const edge_deg = ref([-10, 10, -10, 21, -21, -10, 10, -21, 21, 10, -10, 10])
        const node_top = ref([20, 10, 20, 10, 30, 50, 60, 50, 70, 90, 100, 90, 100])
        const node_left = ref([5, 25, 45, 65, 85, 65, 45, 25, 5, 25, 45, 65, 85])

        const storyIndex = ref(StoryIndex.filter((story) => story.Title.includes('Chapter')))
        const storyFiles = ref(StoryIndex.filter((story) => story.Title.includes('Chapter0'))[0].Stories)
        const storyBooks = ref(StoryIndex.filter((story) => story.Title.includes('Chapter0'))[0].StoryName[language.value])
        const storyName = ref(StoryIndex.filter((story) => story.Title.includes('Event'))[0].EventName[language.value])

        
        const contentFlag = ref(false)
        const autoFlag = ref(false)
        const musicFlag = ref(false)
        const dialogue = ref([])
        const dialogueIndex = ref(0)
        const dialogueCharacter = ref('')
        const dialogueText = ref('')
        const dialogueMusic = ref('')
        const dialogueBackground = ref([])

        var auto

        const preloadImage = (url) => {
            var img=new Image();
            img.src=url;
        }

        const showMainStoryIndex = () => {
            $('#StoryCards').animate({"left":"-100%"}, "slow").removeClass('visible');
            $('#MainStory').animate({"left":"0%"}, "slow").addClass('visible').removeClass('d-none');
        }

        const showSideStoryIndex = () => {
            $('#StoryCards').animate({"left":"-100%"}, "slow").removeClass('visible');
            $('#SideStory').animate({"left":"0%"}, "slow").addClass('visible').removeClass('d-none');
            storyIndex.value = StoryIndex.filter((story) => story.Title.includes("SideStory"))
        }

        const showLibraryIndex = () => {
            $('#StoryCards').animate({"left":"-100%"}, "slow").removeClass('visible');
            $('#Library').animate({"left":"0%"}, "slow").addClass('visible').removeClass('d-none');
            storyIndex.value = StoryIndex.filter((story) => story.Title.includes("Library"))
        }

        const showEventIndex = () => {
            $('#StoryCards').animate({"left":"-100%"}, "slow").removeClass('visible');
            $('#Event').animate({"left":"0%"}, "slow").addClass('visible').removeClass('d-none');
            storyIndex.value = StoryIndex.filter((story) => story.Title.includes("Event"))
        }

        const showStoryIndex = (index) => {
            $('#MainStory').animate({"left":"-100%"}, "slow").removeClass('visible');
            $('#SideStory').animate({"left":"-100%"}, "slow").removeClass('visible');
            $('#Library').animate({"left":"-100%"}, "slow").removeClass('visible');
            $('#Event').animate({"left":"-100%"}, "slow").removeClass('visible');
            $('#StoryIndex').animate({"left":"0%"}, "slow").addClass('visible').removeClass('d-none');
            lastStoryIndex.value = StoryIndex.filter((story) => story.Title.includes(index))[0].Stories.length
            storyFiles.value = StoryIndex.filter((story) => story.Title.includes(index))[0].Stories
            storyBooks.value = StoryIndex.filter((story) => story.Title.includes(index))[0].StoryName[language.value]
        }

        const showStoryContent = (index) => {
            $('#StoryIndex').animate({"left":"-100%"}, "slow").removeClass('visible');
            $('#StoryContent').animate({"left":"0%"}, "slow").addClass('visible').removeClass('d-none');
            $('#controlBar').removeClass('d-none');
            console.log(index)
            dialogueIndex.value = storyFiles.value.indexOf(index)
            axios.get(`json/Story/${index}`)
                .then((res)=>{
                contentFlag.value = true
                if ( res.data.background[0] != "") {
                    background.value = "img/backgrounds/" + res.data.background[0]
                }
                dialogueBackground.value = res.data.background
                dialogueMusic.value = res.data.music
                dialogue.value = res.data.dialogueText[language.value]
                dialogueCharacter.value = res.data.character[language.value]

                setMusic(0)

                preloadImage("img/UI_Icon_VisualNovel_NameBorder.png")
                console.log(dialogueBackground.value)
                for (const bg of dialogueBackground.value) {
                    if (bg != "") {
                        preloadImage("img/backgrounds/" + bg)
                    }
                }
                })
        }

        

        const hide_show = (hide, show) => {
            if (hide == "StoryIndex") {
                contentFlag.value = false
            }
            storyIndex.value = StoryIndex.filter((story) => story.Title.includes(show))
            $('#' + show).animate({"left":"0%"}, "slow").addClass('visible').removeClass('d-none');
            $('#' + hide).animate({"left":"100%"}, "slow").removeClass('visible');
            setTimeout(function(){
                $('#' + hide).addClass('d-none');
            }, 500);
        }

        const hideStoryContent = () => {
            background.value = "img/bg1.png"
            var audio = document.getElementById("storyMusic");
            audio.autoplay = false
            audio.pause()
            $('#StoryContent').html('');
            counter.value = 0
            contentFlag.value = false
            $('#StoryIndex').animate({"left":"0%"}, "slow").addClass('visible');
            $('#StoryContent').animate({"left":"100%"}, "slow").removeClass('visible');
            $('#controlBar').addClass('d-none');
            setTimeout(function(){
                $('#StoryContent').addClass('d-none');
            }, 500);
        }

        const nextStory = () => {
            if (dialogueIndex.value + 1 < storyFiles.value.length) {
                $('#StoryContent').html('');
                counter.value = 0
                dialogueIndex.value += 1;
                axios.get(`json/Story/${storyFiles.value[dialogueIndex.value]}`)
                    .then((res)=>{
                    contentFlag.value = true
                    if ( res.data.background[0] != "") {
                        background.value = "img/backgrounds/" + res.data.background[0]
                    }
                    dialogueBackground.value = res.data.background
                    dialogueMusic.value = res.data.music
                    dialogue.value = res.data.dialogueText[language.value]
                    dialogueCharacter.value = res.data.character[language.value]

                    setMusic(0)
    
                    preloadImage("img/UI_Icon_VisualNovel_NameBorder.png")
                    console.log(dialogueBackground.value)
                    for (const bg of dialogueBackground.value) {
                        if (bg != "") {
                            preloadImage("img/backgrounds/" + bg)
                        }
                    }
                    })
            }
        }

        const displayStory = () => {
            if (contentFlag.value && counter.value < dialogue.value.length) {

                if (dialogueBackground.value[counter.value] != "") {
                    background.value = "img/backgrounds/" + dialogueBackground.value[counter.value]
                }
                if (dialogueMusic.value[counter.value] != "" && counter.value > 0) {
                    setMusic(counter.value)
                }

                if (dialogueCharacter.value[counter.value].length > 0) {
                    $('#StoryContent').append(
                    '<div class="d-flex">' + 
                        '<img src="img/UI_Icon_VisualNovel_NameBorder.png" style="height: 16px;" alt="">' +
                            '<h6 class="text-light px-2">' + 
                                dialogueCharacter.value[counter.value] + 
                            '</h6>' +
                        '<img class="flipX" src="img/UI_Icon_VisualNovel_NameBorder.png" style="height: 16px;" alt="">' +
                    '</div>')

                    $('#StoryContent').append('<p class="text-light dialogue">' + dialogue.value[counter.value] + '</p>')
                }else {
                    $('#StoryContent').append('<p class="dialogue aside">' + dialogue.value[counter.value] + '</p>')
                }


                counter.value += 1
                if (counter.value == dialogue.value.length) {
                    if (autoFlag.value == true) {
                        clearInterval(auto);
                        autoFlag.value = false
                        $('.bi-fast-forward-circle-fill').addClass('text-light').removeClass('text-secondary');
                    }
                    console.log("end")
                }

                document.querySelector("#StoryContent").scrollTop = document.querySelector("#StoryContent").scrollHeight
            }
        }

        const autoplay = () => {
            if (counter.value < dialogue.value.length) {
                $('.bi-fast-forward-circle-fill').addClass('text-secondary').removeClass('text-light');
                auto = setInterval(function(){ 
                    displayStory()
                }, 1500);
                autoFlag.value = true
            }
        }

        const stopAutoplay = () => {
            clearInterval(auto);
            autoFlag.value = false
        }

        const setMusic = (index) => {
            music.value = "music/" + dialogueMusic.value[index]
            var audio = document.getElementById("storyMusic");
            audio.volume = 0.3
            audio.autoplay = false;
            audio.loop = true;
            audio.load();
            if (musicFlag.value == true) {
                audio.autoplay = true;
            }
        }

        const playMusic = () => {
            var audio = document.getElementById("storyMusic");
            music.value = "music/" + dialogueMusic.value[counter.value]
            audio.autoplay = true
            audio.play()
            musicFlag.value = true
        }

        const stopMusic = () => {
            var audio = document.getElementById("storyMusic");
            audio.autoplay = false
            audio.pause()
            musicFlag.value = false
        }

        onUnmounted(() => {
            background.value = ""
        })


        return {
            isMobile,
            text,
            language,
            music,
            lastMainStory,
            storyIndex,
            lastStoryIndex,
            dialogueIndex,
            autoFlag,
            musicFlag,
            storyFiles,
            storyBooks,
            edge_top,
            edge_left,
            edge_deg,
            node_top,
            node_left,
            showMainStoryIndex,
            showSideStoryIndex,
            showLibraryIndex,
            showEventIndex,
            showStoryIndex,
            showStoryContent,
            hide_show,
            hideStoryContent,
            displayStory,
            nextStory,
            autoplay,
            stopAutoplay,
            playMusic,
            stopMusic,

            dialogue
        }
    }
}
</script>

<style>

#StoryBlock{
  height: 70vh;
  position: relative;
  overflow-x: hidden;
  overflow-y: visible;
  margin-bottom: 5rem;
}
#StoryCards {
    width: 100%;
    position:absolute;
}
#MainStory, #SideStory, #Library, #Event {
    width:100%;
    position:absolute;
    left:100%;
}
#StoryIndex {
    width:100%;
    position:absolute;
    left:100%;
}
#StoryContent {
    width:100%;
    position:absolute;
    left:100%;
}
.road {
    display: flex;
    position: absolute;
    background: #fff;
    width: 20%;
    height: 5px;
    z-index: 150;
}
.road::before {
    content: '';
    position: absolute;
    top: -1px;
    left: -1px;
    right: -1px; 
    bottom: -1px;
    background: #fff;
    z-index: 140;
}
.road::after {
    content: '';
    position: absolute;
    top: -1px;
    left: -1px;
    right: -1px; 
    bottom: -1px;
    background: #7ff4ff;
    filter: blur(10px);
    z-index: 130;
}
.road .glass {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
}
.road:nth-child(1)::before,
.road:nth-child(1)::after {
    background: linear-gradient(0deg, #7ff4ff, #fff, #7ff4ff);
}
.road .edge {
    color: #fff;
    margin-top: -10px;
}
.book {
    position: absolute;
    width: 10%;
    --bs-bg-opacity: 1;
    background-color: transparent!important;
    text-align: center!important;
    border: 0!important;
    z-index: 160;
}
.flipX {
    transform: rotateY(180deg);
}
.dialogue {
    font-size: 1.2rem;
    margin-bottom: 2rem;
}
.dialogue:nth-of-type(1) {
    margin-top: 2rem;
}
.aside {
    color: rgb(255,200,80)
}
.controlBtn {
    position: absolute;
    font-size: 2rem;
    top: 5%;
}
.controlBtn:nth-of-type(1) {
  right: 20%;
}
.controlBtn:nth-of-type(2) {
  right: 15%;
}
.controlBtn:nth-of-type(3) {
  right: 10%;
}
.controlBtn:nth-of-type(4) {
  right: 5%;
}
@media screen and (max-width: 768px) {
    .controlBtn:nth-of-type(1) {
        right: 80%;
    }
    .controlBtn:nth-of-type(2) {
        right: 45%;
    }
    .controlBtn:nth-of-type(3) {
        right: 25%;
    }
    .controlBtn:nth-of-type(4) {
        right: 5%;
    }
}
.unselectable {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
</style>
