<template>
    <div class="container">
        <CampaignStory/>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, watch } from 'vue'
import { useLanguage } from '@/store/Language'
import { storeToRefs } from 'pinia'
import CampaignStory from '@/components/CampaignStory.vue'

export default {
    name: 'StoryView',
    components: {
        CampaignStory,
    },
    setup() {
        const { language } = storeToRefs(useLanguage())
        useLanguage().changeLanguage()
        watch(language, () => {
            useLanguage().changeLanguage()
        })

        
    }
}
</script>

